.empty-result {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  padding-top: 65px;
  text-align: center;
}

/* 회색 동그라미 안의 느낌표 */
.empty-icon {
  width: 72px;
  height: 72px;
  border-radius: 50%;
  border: 4px solid #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 16px;
}

.empty-icon span {
  font-size: 55px;
  color: #ddd;
}

/* 메인 텍스트 */
.empty-title {
  font-size: 16px;
  font-weight: 800;
  margin-bottom: 10px;
  color: #333;
}

/* 서브 텍스트 */
.empty-desc {
  font-size: 13px;
  color: #888;
  line-height: 1.5;
  margin-bottom: 20px;
}

/* 초록 버튼 */
.empty-btn {
  background: #008a69;
  color: #fff;
  border: none;
  border-radius: 20px;
  padding: 7px 20px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

.recommend-title {
  margin: 50px 0 12px;
  font-size: 15px;
  font-weight: 800;
  text-align: left;
  width: 100%;
}

.recommend-list {
  display: flex;
  gap: 12px;
  overflow-x: auto; /* 가로 스크롤 가능 */
  /* padding-bottom: 8px; */
}

/* 결과 있음 */
.result-section {
  padding: 16px;
}

/* 연관 태그 */
.related-tags {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 16px;
  flex-wrap: wrap;
}

.tag-label {
  font-size: 14px;
  font-weight: 800;
  color: #000000;
  margin-right: 10px;
}

.tag-btn {
  background: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 16px;
  padding: 4px 12px;
  font-size: 13px;
  color: #555;
  cursor: pointer;
}

.tag-btn:hover {
  background: #e8e8e8;
}

.result-divider {
  height: 3px;
  background-color: #d9d9d9;
  margin: 12px 0 16px;

  /* 패딩 무시하고 부모 영역 꽉 채우기 */
  margin-left: -16px;
  margin-right: -16px;
}
