.search-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 0 20px;
  margin-top: 20px;
  box-sizing: border-box;
}

.search-input {
  width: 100%;
  height: 40px;
  padding: 0 45px 0 15px;
  border: 1px solid #ddd;
  border-radius: 25px;
  background-color: #f7f7f7;
  font-size: 16px;
  color: #333;
}

.search-input::placeholder {
  color: #999;
  font-size: 12px;
}

.search-input:focus {
  outline: none;
  border-color: #008a69;
  box-shadow: 0 0 0px #0aa37f;
}

.search-icon {
  position: absolute;
  right: 35px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  background: none; /* 배경 제거 */
  border: none; /* 테두리 제거 */
}
.clear-btn {
  position: absolute;
  right: 60px; /* 돋보기 왼쪽 */
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: none;
  background-color: #bbb; /* 회색 배경 */
  color: white; /* X 색상 */
  font-size: 11px;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}
