.collection-item {
  display: flex;
  align-items: center;
  background: #fff;
  border-radius: 10px;
  padding: 10px;
  margin-bottom: 20px;
  position: relative;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}

.collection-item__img {
  width: 80px;
  height: 80px;
  border-radius: 8px;
  object-fit: cover;
  margin-right: 12px;
}

.collection-item__info {
  flex: 1;
}

.collection-item__title {
  font-size: 15px;
  font-weight: 600;
  margin-bottom: 10px;
}

.collection-item__tags {
  font-size: 13px;
  color: #777;
  margin-bottom: 20px;
}

/* 삭제 버튼 (우상단) */
.collection-item__remove {
  position: absolute;
  top: 8px;
  right: 8px;
  border: none;
  background: none;
  cursor: pointer;
  color: #aaa;
}

/* 하트 버튼 (우하단) */
.collection-item__heart {
  position: absolute;
  bottom: 8px;
  right: 8px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  color: #ccc;
}

.collection-item__heart.liked {
  color: #e74c3c;
}
/* 저장 아이콘 (변환 레시피) */
.collection-item__bookmark {
  position: absolute;
  bottom: 8px;
  right: 8px;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  color: #ccc;
}

.collection-item__bookmark.saved {
  color: #f1c40f; /* 노란색 */
}
