/* 카드 스타일 제거 + 상단 경계선만 */
.main-trans {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  width: 100%;
  padding: 12px 0; /* 위아래 여백만 */
  background: transparent; /* 카드 배경 제거 */
  border-radius: 0; /* 둥근모서리 제거 */
  box-shadow: none; /* 그림자 제거 */
  /* border-top: 1px solid var(--divider, #e5e7eb); ⬆ 상단 경계선만 */

  box-sizing: border-box;
  overflow: visible;
}

/* 컨텐츠는 기존 그대로 사용 */
.main-trans__icon {
  width: 60px;
  height: 60px;
  flex: 0 0 60px;
  object-fit: contain;
}
.main-trans__texts {
  flex: 1 1 auto;
  min-width: 0;
}
.main-trans__title {
  margin: 0 0 6px;
  font-weight: 800;
  font-size: 14px;
  white-space: normal; /* ✅ 줄바꿈 허용 */
  overflow: visible; /* ✅ 잘리지 않게 */
  text-overflow: unset; /* ✅ 말줄임 해제 */
  line-height: 1.4;
}
.main-trans__subtitle {
  margin: 0;
  font-size: 14px;
  color: #6b7280;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.main-trans__chevron {
  margin-left: 8px;
  font-size: 22px;
  color: #9aa0a6;
  flex: 0 0 auto;
}

/* 아주 작은 화면 대응 */
@media (max-width: 360px) {
  .main-trans__icon {
    width: 36px;
    height: 36px;
    flex-basis: 36px;
  }
  .main-trans__title {
    font-size: 15px;
  }
  .main-trans__subtitle {
    font-size: 13px;
  }
}
.main-trans {
  text-decoration: none; /* ✅ 밑줄 제거 */
  color: inherit; /* ✅ 텍스트 색상은 상속 */
}
