.banner-container {
  width: 90%;
  max-width: 600px;
  height: 180px;
  margin: 20px auto;
  padding: 20px;
  background-color: #f7efe3;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  overflow: hidden;
  border: 1px solid #d9d9d9;
  box-shadow: 0 0px 2px rgba(0, 0, 0, 0.25);
}

.text-button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  /* margin-top: 10px; */
  z-index: 1;
}

.main-title {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  margin: 0 0 7px 0;
  line-height: 1.4;
  /* gap: 10px; */
}

.main-text1 {
  margin-bottom: 5px;
}

.main-text2 {
  color: #008a69;
  font-size: 16px;
}

.sub-text {
  font-size: 12px;
  color: #666;
  margin: 0 0 13px 0;
  line-height: 1.4;
}

.convert-button {
  background-color: #008a69;
  color: white;
  padding: 7px 30px;
  border: none;
  border-radius: 25px;
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  /* box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); */
  transition: transform 0.2s;
}

.convert-button:hover {
  transform: translateY(-2px);
}

.banner-image {
  position: absolute;
  /* display: flex; */
  align-items: center;
  right: 0px;
  bottom: 0;
  height: 200px;
  width: 200px;
  object-fit: contain;
}
