.category-item-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 80px;
  margin: 10px;
  cursor: pointer;
}

.category-image {
  width: 60px;
  height: 60px;
  border: 1px solid #ddd;
  border-radius: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s, box-shadow 0.2s;
}

.category-image:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.category-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.category-name {
  margin-top: 8px;
  font-size: 14px;
  color: #000000;
  font-weight: bold;
}
