/* ===== Footer 컨테이너 ===== */
.footer {
  position: absolute; /*앱 프레임 내부 고정
  /* position: fixed; */
  inset-inline: 0;
  bottom: max(9px, env(safe-area-inset-bottom));
  width: min(96%, var(--app-max, 390px));
  margin: 0 auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  padding: 0 16px;
  z-index: 5;
}

/* ===== Footer 아이템 ===== */
.footer-item {
  --label-muted: #6b7280;
  --label-active: #111827;

  flex: 1 1 0;
  min-width: 0;
  text-align: center;
  cursor: pointer;
  user-select: none;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  outline: none;
}

.footer-item:focus-visible {
  box-shadow: 0 0 0 2px rgba(16, 185, 129, 0.35) inset;
  border-radius: 12px;
}

.footer-item img {
  width: 28px;
  height: 28px;
  margin-bottom: 4px;
}

.footer-label {
  font-size: 12px;
  line-height: 1;
  color: var(--label-muted);
  font-weight: 700;
  white-space: nowrap;
}

.footer-item.is-active .footer-label {
  color: var(--label-active);
}

/* ===== 중앙 FAB 버튼 ===== */
.footer-center {
  position: relative;
  top: -5px;
  background-color: #008a69;
  border-radius: 50%;
  box-shadow: 0 3px 10px rgba(0, 138, 105, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 70px;
  flex: none;
  margin-inline: 8px;
}

.footer-center img {
  width: 55px;
  height: 55px;
  object-fit: cover;
  border-radius: 30%;
}

/* 중앙 FAB은 라벨 감춤 */
.footer-center .footer-label {
  display: none;
}
