/* 컨테이너 */
.tm-wrap {
  padding: 0 0 20px;
}

.tm-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  margin: 8px 0;
}
.tm-title {
  margin: 0;
  font-size: 16px;
  font-weight: 800;
  color: #008a69;
}
.tm-link {
  border: 0;
  background: transparent;
  color: #9ca3af;
  font-size: 13px;
  display: flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
}

/* ===== Carousel (접두사 tmc-) ===== */
.tmc {
  position: relative;
  user-select: none;
}
.tmc-viewport {
  position: relative;
  overflow: hidden;
  /* 높이 깜빡임 방지를 위한 최소 높이 */
  min-height: 220px;
}
.tmc-track {
  display: flex;
  transition: transform 0.35s ease;
  will-change: transform;
  touch-action: pan-y;
}
.tmc-track.tmc-dragging {
  transition: none;
}
.tmc-slide {
  min-width: 100%;
  flex: 0 0 100%;
  padding: 0;
  box-sizing: border-box;
}

/* 좌/우 버튼 */
.tmc-nav {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: 0;
  width: 40px; /* ✅ 크기 확대 */
  height: 40px; /* ✅ 크기 확대 */
  border-radius: 50%; /* 완전 원형 */
  background: rgba(0, 0, 0, 0.25); /* ✅ 배경 더 진하게 */
  color: #fff; /* ✅ 아이콘을 흰색으로 */
  font-size: 20px; /* ✅ 아이콘 크기 살짝 키움 */
  line-height: 1;
  display: grid;
  place-items: center;
  cursor: pointer;
  z-index: 2;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15); /* ✅ 그림자 추가 */
  transition: background 0.2s ease, transform 0.2s ease;
}
.tmc-nav:hover:not(:disabled) {
  background: rgba(0, 0, 0, 0.45); /* ✅ hover 시 더 진하게 */
  transform: translateY(-50%) scale(1.1); /* ✅ hover 시 살짝 커짐 */
}
.tmc-nav:disabled {
  opacity: 0.3;
  cursor: default;
}
.tmc-nav--prev {
  left: 10px; /* 살짝 띄워 배치 */
}
.tmc-nav--next {
  right: 10px;
}

/* 인디케이터 */
.tmc-dots {
  display: flex;
  justify-content: center;
  gap: 6px;
  margin-top: 20px;
}
.tmc-dot {
  width: 6px;
  height: 6px;
  border-radius: 999px;
  background: #d1d5db;
  border: 0;
  padding: 0;
  cursor: pointer;
}
.tmc-dot.active {
  width: 18px;
  background: #008a69;
}

/* ===== 카드(공통) ===== */
.tm-card {
  width: 100%;
  background: #fff;
  border-radius: var(--card-radius, 16px);
  /* box-shadow: var(--shadow, 0 4px 16px rgba(0, 0, 0, 0.08)); */
  /* padding: 12px; */
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 12px;
  box-sizing: border-box;
}
.tm-media-wide {
  display: flex;
  justify-content: center;
  align-items: center;
}
.tm-media-wide img {
  width: 100%;
  aspect-ratio: 16/9;
  object-fit: cover;
  border-radius: 12px;
  max-height: unset;
  -webkit-user-drag: none;
  /* user-drag: none; */
}

.tm-body {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.tm-headline {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 8px;
}
.tm-name {
  margin-left: 10px;
  font-size: 16px;
  font-weight: 800;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.tm-sub {
  margin-left: 10px;
  color: #374151;
  font-size: 13px;
  line-height: 1.35;
  min-width: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tm-chip {
  margin-right: 7px;
  border: 0;
  border-radius: 999px;
  padding: 6px 12px;
  background: #008a69 !important;
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  white-space: nowrap;
  cursor: pointer;
  flex: 0 0 auto;
}

/* 모션 최소화 */
@media (prefers-reduced-motion: reduce) {
  .tm-info-toggle .tm-caret,
  .tm-collapse,
  .tmb-fill,
  .tmc-track {
    transition: none;
  }
}

/* 좁은 화면 대응 */
@media (max-width: 380px) {
  .tm-card {
    padding: 10px;
  }
  .tm-name {
    font-size: 15px;
  }
  .tm-sub {
    font-size: 12.5px;
  }
  .tmb-track {
    height: 7px;
  }
}
