/* 1) 화면 전체 중앙 고정 배치 */
.app-wrap {
  position: fixed;
  inset: 0;

  /* ✅ 여기서 핵심 */
  /* width: 100vw;  ← 지우세요. 스크롤바 폭까지 먹어서 잘림 유발 */
  /* 대신 패딩으로 여백을 확보해 박스/그림자까지 안전하게 보이게 */
  padding: max(12px, env(safe-area-inset-top))
    max(12px, env(safe-area-inset-right)) max(12px, env(safe-area-inset-bottom))
    max(12px, env(safe-area-inset-left));
  box-sizing: border-box;

  display: grid;
  place-items: center;
  height: 100svh;
  height: 100dvh; /* 폴백 */
  background: #ececec;
  overflow: hidden;
}

/* 2) 앱 박스 */
.app {
  /* ✅ 고정폭 대신, 뷰포트에 맞춰 줄어들되 최대 402px */
  width: min(402px, 100%);
  /* 그림자까지 잘 보이도록 래퍼 패딩으로 이미 여백 확보됨 */
  height: min(100svh, 800px);
  height: min(100dvh, 800px);

  max-height: none;
  background: #fff;
  border-radius: 29px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

/* 3) 내부 스크롤 영역 */
.content-scroll {
  min-height: 0;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  background: #fff;

  /* 스크롤바 등장/퇴장에도 폭 안 흔들리게 */
  scrollbar-gutter: stable;
  overscroll-behavior: contain;
}

/* (참고) 스크롤바 스타일 그대로 유지 */
.content-scroll::-webkit-scrollbar {
  width: 4px;
}
.content-scroll::-webkit-scrollbar-track {
  background: transparent;
}
.content-scroll::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.content-scroll::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

.footer {
  flex-shrink: 0;
}
