.meal-page {
  position: relative;
  background-color: white;
  min-height: 100vh;
  overflow: hidden;
  margin-top: 16px; /* Header와 Calendar 사이 간격 */
}

.meal-bg {
  position: absolute;
  top: 300px; /* 헤더+캘린더 높이에 따라 조절 */
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #faf4ec; /* 연한 배경 */
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  z-index: 0;
}

.meal-content {
  position: relative;
  z-index: 1;
  padding: 0 16px;
}
