/* ===== 전역 리셋 ===== */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html,
body,
#root {
  height: 100%;
}
body {
  background: #ffffff;
  color: #111;
}

/* ===== 폰트 로드 ===== */
/* 로고 전용: LOTTERIADDAG (롯데리아 딱붙어체) */
@font-face {
  font-family: "LOTTERIADDAG";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/LOTTERIADDAG.woff2")
    format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

/* 본문: 나눔스퀘어라운드 (Regular, ExtraBold) */
@font-face {
  font-family: "NanumSquareRound";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2107@1.1/NanumSquareRound.woff")
    format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "NanumSquareRound";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2107@1.1/NanumSquareRoundEB.woff")
    format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

/* 기본 본문 폰트는 나눔스퀘어라운드 */
body {
  font-family: "NanumSquareRound", -apple-system, BlinkMacSystemFont, "Segoe UI",
    Roboto, Helvetica, Arial, "Apple Color Emoji", "Segoe UI Emoji";
}
