/* ===== 전역: 크기 계산을 border-box로 통일 ===== */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* 브라우저 기본 마진 제거(필요시) */
html,
body {
  margin: 0;
  padding: 0;
}

/* ===== 컨테이너 ===== */
.surveyContainer {
  font-family: "Noto Sans KR", sans-serif;
  color: #000000;
}

/* 중앙 컨텐츠 */
.contentWrap {
  width: 100%;
  max-width: clamp(360px, 92vw, 500px);
  margin: 0 auto;
  padding: 16px 20px 0px; /* 하단 버튼 자리 확보 */
  text-align: left;
}

/* ===== 헤더 ===== */
.headerPlaceholder {
  height: 52px; /* 헤더 높이만 확보 (실제 버튼/타이틀 없음) */
  flex-shrink: 0;
}

/* ===== 페이지 표시 ===== */
.pageIndicator {
  margin: 0 0 16px;
  font-weight: 800;
  font-size: 20px;
  color: #ff8000;
  text-align: left;
}

/* ===== 안내 / 타이틀 / 질문 ===== */
.guideText {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.6;
  margin: 0 0 20px;
  color: #000000;
}

.title {
  font-size: 17px;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 8px;
}

.question {
  font-size: 14px;
  font-weight: 500;
  margin: 14px 0 6px;
  padding-right: 8px;
}

.desc {
  font-size: 12px;
  color: #7c7c7c;
  margin: 0 0 16px;
}

.helperText {
  font-size: 13px;
  color: #7c7c7c;
  font-weight: 400;
  line-height: 1.5;
  margin: 2px 0 8px;
}

/* ===== 질문 블록 ===== */
.questionBlock {
  margin-bottom: 28px; /* 질문 세트끼리 간격 */
}

.questionSub {
  font-size: 15px;
  font-weight: 500;
  color: #444444;
  margin: 0 0 10px;
  line-height: 1.5;
}

.buttonGroup {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 14px;
  margin-bottom: 16px;
}

/* ===== 섹션/라벨 ===== */
.section {
  margin-bottom: 20px;
}
.section label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 600;
  color: #7c7c7c;
}

/* ===== 폼 요소 공통 ===== */
input,
select,
textarea,
button {
  font-family: inherit;
  font-size: 14px;
  line-height: 1.2;
}

/* 숫자 인풋 스핀 제거 */
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  appearance: textfield;
}

/* ===== 입력 필드 ===== */
.inputField {
  width: 100%;
  height: 46px;
  border-radius: 12px;
  border: 1.5px solid #c7c7c7;
  outline: none;
  font-size: 14px;
  padding: 0 14px;
  transition: border-color 0.15s ease, box-shadow 0.15s ease;
  /* 핵심: padding/보더 포함하여 100% 계산 */
  box-sizing: border-box;
}

.inputField:focus {
  border-color: #008a69;
  box-shadow: 0 0 0 4px rgba(0, 138, 105, 0.15);
}

/* ===== 선택 버튼 ===== */
.optionsGrid {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 12px 16px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.optionBtn {
  min-width: 96px;
  height: 42px;
  padding: 0 16px;
  border-radius: 22px;
  border: 1.5px solid #c7c7c7;
  background: #ffffff;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.15s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  white-space: nowrap;
}

.optionBtn:hover {
  border-color: #7c7c7c;
}

.selected {
  border-color: #008a69;
  background: #baf8e9;
  color: #008a69;
  font-weight: 700;
}

/* ===== 에러 ===== */
.error {
  margin-top: 6px;
  font-size: 12px;
  color: #dc2626;
}
.invalid {
  border-color: #dc2626 !important;
}

.bottomBar {
  background: #ffffff;

  padding: 16px 20px calc(16px + env(safe-area-inset-bottom));
  z-index: 10;
}

/* ===== CTA 버튼 ===== */
.prevBtn,
.nextBtn {
  position: static;
  margin-top: 30px;
  width: 50%;
  height: 50px;
  border-radius: 99px;
  font-weight: 800;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid transparent;
  padding: 0 28px;
}

.prevBtn {
  background: #ffffff;
  color: #008a69;
  border-color: #008a69;
}

.nextBtn {
  background: #008a69;
  color: #ffffff;
  border-color: #008a69;
}

.nextBtn:disabled {
  background: #c7c7c7;
  border-color: #c7c7c7;
  cursor: not-allowed;
}
/* inline style로도 충분하지만 모듈로 쓰고 싶다면 */
.infoPill {
  display: inline-block;
  padding: 4px 10px;
  margin: 4px 0 10px;
  border-radius: 999px;
  background: #eafbef;
  color: #10b981;
  font-weight: 700;
  font-size: 12px;
}
